body {
  margin: 0;
  padding: 0;
  font-family: 'Salesforce Sans', Arial, sans-serif !important;
  padding-bottom: 70px;
}

.section-title {
  display: block;
  font-size: 16px;
  font-weight: 400;
  margin-bottom: 10px;
}

.section-container {
  padding: 10px 20px;
}


.fe-general-input-container {
  margin: 15px 0;
  width: 800px;
}

.fe-general-input-label {
  display: block;
  font-size: 13px;
  font-weight: 600;
}

.fe-general-input-textarea {
  height: 50px;
  resize: none;
}

.fe-general-input-textarea,
.fe-general-input-text {
  display: block;
  font-size: 13px;
  border: 1px solid #d8dde7;
  border-radius: 4px;
  padding: 6px 8px;
  width: 550px;
  margin: 5px 0;
}

.fe-select {
  font-size: 13px;
  border: 1px solid #d8dde7;
  border-radius: 4px;
  background: #fff;
  height: 28px;
  margin: 0 10px 5px 0;
}


.dhtmlxcalendar_month_arrow {
  cursor: pointer;
}

#FilterAppRoot {
  height: calc(100% - 76px);
  position: fixed;
  width: 100%;
  overflow-y: auto;
}