.df-explain {
    font-size: 14px;
    width: 600px;
    line-height: 21px;
    margin-bottom: 10px;
}

.df-FieldsContainer {
    width: 640px;
    margin: 15px 0;
}

.df-FieldsContainer span {
    display: inline-block;
    width: 200px;
    margin-bottom: 7px;
}

.df-FieldsContainer input {
    margin-right: 6px;
}


.df-select {
    border: 1px solid #d8dde6;
    border-radius: 4px;
    width: 220px;
    font-size: 14px;
    outline: 0;
    height: 225px;
}

.df-select option {
    padding: 8px;
    font-size: 13px;
}

.df-actions-container {
    display: inline-block;
    position: relative;
    top: -167px;
    padding: 15px;
}

.df-action-button {
    padding: 5px;
    border-radius: 3px;
    border: 1px solid #d8dde6;
    margin-bottom: 10px;
    cursor: pointer;
    width: 40px;
    text-align: center;
    user-select: none;
}

.rotate-up {
    transform: rotate(90deg);
}

.rotate-down {
    transform: rotate(-90deg);
}
